<template>
  <section class="center-pass-goals">
    <div class="container mx-auto p-1 sm:mx-1 max-w-screen-md">
      <div class="w-full h-auto">
        <div class="flex flex-col space-y-1">
          <p class="text-slate-600 font-bold text-end justify-end w-full">گل + پاس گل</p>
          <Suspense>
            <template #default>
              <NumberTotal :menu="list"/>
            </template>
            <template #fallback>
              <LoaderComponentTwo/>
            </template>
          </Suspense>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import{reactive,onMounted} from "vue";
import{defineAsyncComponent} from "vue";
import EndPoints from "@/service/EndPoints";
import {handleError} from "@/components/composable/handleError";
const LoaderComponentTwo=defineAsyncComponent(()=>import('@/components/composable/LoaderComponentTwo'))
const NumberTotal=defineAsyncComponent(()=>import('@/components/main/competitions/NumberTotal'))
export default {
  name: "TotalCompetition",
  components: {NumberTotal, LoaderComponentTwo},
  setup(){
    const list=reactive([])
    const totalInfo=onMounted(async ()=>{
      await EndPoints.getPersianGulfs()
      .then((response)=>{
        if(response.data.success){
          list.push(response.data.data.assist_goal)
        }
      })
      .catch((error)=>{
        handleError(error)
      })
    })
    return{
      list,
      totalInfo,
    }
  }
}
</script>

